.pdfFrameContainer{
    width: 100%;
    display: flex;
    margin: auto;
}

.pdfFrame{
    width: 100%;
    height: 100vh;
}

.pdfViewer-back{
    font-size: 40px;
    padding: 5px;
    align-self: baseline;
    margin: 0 100px;
    user-select: none;
    color: #feca64;
    cursor: pointer;
}

.pdfViewer-back:hover{
    color: #ddb056;
}